/**
 * Internal dependencies
 */
import { processLayout } from './admin-calendar-layout.js';

/**
 * Popper implementation
 */
jQuery( function( $ ) {

	function createFragment( el, text, attr ) {

		let _el = $( '<' + el + '/>' );

		function isHTML( str ) {
			return ( str.indexOf( '</' ) !== -1 || str.indexOf( '/>' ) !== -1 );
		}

		if ( text && text.length && ! isHTML( text ) ) {
			_el.text( text );
		}

		if ( text && text.length && isHTML( text ) ) {
			_el.html( text );
		}

		if ( attr ) {
			_el.attr( attr );
		}

		return _el;

	}

	/**
	 * Scrolls to first booking on day view.
	 *
	 * @since 1.13.0
	 */
	 function scrollToFirstBooking() {
		// Day view and atleast one booking.
		if ( $( '.calendar_days' ).length && $( '.daily_view_booking' ).length ) {
			let previousStartValue,
				smallestStartValueIndex;

			// Gather all start values.
			$( '.daily_view_booking' ).each( function( index ) {
				let currentStartValue = parseInt( $( this ).data( 'booking-start' ), 10 );

				if ( 'undefined' === typeof previousStartValue ) {
					previousStartValue = currentStartValue;
				}

				if ( currentStartValue <= previousStartValue ) {
					smallestStartValueIndex = index;
				}

				previousStartValue = currentStartValue;
			} );

			/*
			 * Keep running at interval until first booking
			 * offset is found to prevent race condition.
			 */
			let findFirstBooking = setInterval( function() {
				let firstBookingTopOffset = parseInt( $( '.daily_view_booking' ).eq( smallestStartValueIndex ).offset().top, 10 );

				if ( firstBookingTopOffset > 0 ) {
					$( 'html, body' ).animate( {
						scrollTop: firstBookingTopOffset - 110
					}, 600 );

					clearInterval( findFirstBooking );

				}
			}, 100 );

			/*
			 * Fallback timeout after 5 seconds to
			 * clear the interval so it doesn't continue
			 * running indefinitely.
			 */
			setTimeout( function() {
				clearInterval( findFirstBooking );
			}, 5000 );
		}
	}

	$( '.daily_popover' )
		.off( '.daily_close_handler' )
		.on( 'click.daily_close_handler', '#event_detail_header span', function() {
			$( '.daily_popover' ).remove();
		} );

	$( '.calendar_days' )
		.off( '.daily_block_handler' )
		.on( 'click.daily_block_handler', 'ul.bookings li', function() {

			let selected_booking = $( this );

			if ( $( '#daily_popover_' + selected_booking.data( 'booking-id' ) ).length ) {
				return;
			} else {
				$( '.daily_popover' ).remove();
			}

			let header_fragment = createFragment( 'div', '', {
				id: 'event_detail_header'
			} );

			let footer_fragment = createFragment( 'div', '', {
				id: 'event_detail_footer'
			} );

			let body_fragment = createFragment( 'div', '', {
				id: 'event_detail_body'
			} );

			header_fragment
				.append( createFragment( 'span', 'X' ) )
				.append( createFragment( 'h3', selected_booking.data( 'booking-title' ) ) );

			footer_fragment
				.append( createFragment( 'a', wc_bookings_admin_js_params.i18n_view_details, {
					href: selected_booking.data( 'booking-url' )
				} ) )
				.append( createFragment( 'span' ), '&rarr;' );

			let ul_fragment = createFragment( 'ul' );

			if ( selected_booking.data( 'booking-date' ) && selected_booking.data( 'booking-time' ) ) {
				let item1 = createFragment( 'li', '<span/>' + '<strong>' + selected_booking.data( 'booking-date' ) + '<br/>' + selected_booking.data( 'booking-time' ) + '</strong>', {
					class: 'daily_popover_datetime_icon'
				} );
				ul_fragment.append( item1 );
			}

			if ( selected_booking.data( 'booking-customer' ) ) {
				let item2 = createFragment( 'li', '<span/>' + createFragment( 'strong', wc_bookings_admin_js_params.i18n_customer ).get( 0 ).outerHTML + selected_booking.data( 'booking-customer' ), {
					class: 'daily_popover_customer_icon'
				} );
				ul_fragment.append( item2 );
			}

			if ( selected_booking.data( 'booking-resource' ) ) {
				let item3 = createFragment( 'li', '<span />' + createFragment( 'strong', wc_bookings_admin_js_params.i18n_resource ).get( 0 ).outerHTML + selected_booking.data( 'booking-resource' ).split( ',' ).join( '<br/>' ), {
					class: 'daily_popover_resource_icon'
				} );
				ul_fragment.append( item3 );
			}

			if ( selected_booking.data( 'booking-persons' ) ) {
				let item4 = createFragment( 'li', '<span />' + createFragment( 'strong', wc_bookings_admin_js_params.i18n_persons ).get( 0 ).outerHTML + selected_booking.data( 'booking-persons' ), {
					class: 'daily_popover_persons_icon'
				} );
				ul_fragment.append( item4 );
			}

			body_fragment.append( ul_fragment );

			let selectedBookingPassMiddle = isPassMiddle( selected_booking );
			let output_fragment = $( '<div/>' )
				.attr( {
					id: 'daily-popover-' + selected_booking.data( 'booking-id' ),
					class: 'daily_popover'
				} )
				.css( {
					left: getPopoverPositionLeft( selected_booking, selectedBookingPassMiddle ),
					top: getPopoverPositionTop( selected_booking )
				} )
				.append( header_fragment, body_fragment );
				if ( '' !== selected_booking.data( 'booking-url' ) ) {
					output_fragment.append( footer_fragment );
				}

			$( '.calendar_days' ).prepend( output_fragment );

			let bottom_offset = ( output_fragment.height() + output_fragment.position().top ) - ( $( '.calendar_days' ).height() - 20 );

			if ( bottom_offset > 0 ) {
				$( '.daily_popover' )
					.css( 'margin-top', '-' + bottom_offset + 'px' );
			}

		} );

		/*
		 * Add select class on hover event for each event with the same 
		 * event( Bookings booking or Google Calendar event ) ID
		 */
		$('.calendar_month_event').hover( 
			function() {
				let id = this.dataset["id"];
				let id_class = '.calendar_event_id_' + id;
				$( id_class ).find( 'a' ).addClass("calendar_month_event_selected");
			},
			function() {
				let id = this.dataset["id"];
				let id_class = '.calendar_event_id_' + id;
				$( id_class ).find( 'a' ).removeClass("calendar_month_event_selected");
			},
		);

	/**
	 * Checks if the middle point of the selected booking
	 * passes the middle point of the calendar container.
	 *
	 * @since 1.13.0
	 * @param selected_booking
	 * @return bool
	 */
	function isPassMiddle( selected_booking ) {
		let containerMiddle = $( '.calendar_days' ).width() / 2;

		// If selected booking starts on or after middle, its passed.
		if ( selected_booking.position().left >= containerMiddle ) {
			return true;
		}

		if ( ( selected_booking.width() - ( containerMiddle - selected_booking.position().left ) ) + selected_booking.position().left > containerMiddle ) {
			return true;
		}

		return false;
	}

	function getPopoverPositionLeft( el, selectedBookingPassMiddle ) {
		const singleElement = 1 === $( el ).parent( 'ul.bookings' ).find( 'li' ).length ? true : false;

		// If only one booking event thus full width.
		if ( singleElement ) {
			return ( el.width() / 2 ) - 130;
		}

		if ( selectedBookingPassMiddle ) {
			return el.position()[ 'left' ] - 245;
		} else {
			return el.position()[ 'left' ] + parseInt( el.parent().css( 'margin-left' ), 10 ) + el.width() + 20;
		}
	}

	function getPopoverPositionTop( el ) {
		return el.position()[ 'top' ] + parseInt( el.parent().css( 'margin-top' ), 10 );
	}

	$( '.calendar_days' )
		.off( '.daily_calendar_handler' )
		.on( 'click.daily_calendar_handler', function( e ) {

			if ( $( e.target ).closest( 'li' ).length ) {
				return;
			}

			$( '.daily_popover' ).remove();

		} );

	let input = $.map( $( '.daily_view_booking' ), function ( el ) {
		return {
			id: $( el ).data( 'booking-id' ),
			start: $( el ).data( 'booking-start' ),
			end: $( el ).data( 'booking-end' ),
		};
    } );
    
    let totalCalendarHeight = 1968; // definded by design
    let calendarHeihgtInMinutes = 1440; // 60 minutes * 24 hours
    let scale = totalCalendarHeight / calendarHeihgtInMinutes;

	processLayout( input, 1968, 100 ).forEach( function( node ) {
		let $el = $('*[data-booking-id="' + node.id + '"]');

        $el.css( { 'top':  'calc(' + node.top * scale + 'px + 2px )' } );
		$el.css( { 'height': 'calc(' + node.height * scale +'px - 3px )' } );
		$el.css( 'left', node.left + '%' );
		// 12px come from the popper padding
		$el.css( { 'width': 'calc(' + node.width + '% - 13px )' } );
		$el.show();
	}, scrollToFirstBooking() );

	let global_availability = $.map( $( '.daily_view_global_availabiltiy' ), function ( el ) {
		return {
			start: $( el ).data( 'start' ),
			end: $( el ).data( 'end' ),
			id: $ (el ).data( 'global-availability-id' ),
		};
	});

	global_availability.forEach( function ( range ) {
		let $el = $( '*[data-global-availability-id="' + range.id + '"]' );
		$el.css({ 'top': 'calc(' + range.start * scale + 'px )' });
		$el.css({ 'height': 'calc(' + (range.end - range.start) * scale + 'px )' });
		$el.css( 'left', '-43px' );
		$el.css({ 'width': 'calc(' + '100' + '% + 50px )' });
		$el.show();
	});  


	$( '.daily_view_booking' )
		.each( function( index, el ) {
			let $el = $( el );

			$el.html(
				'<strong>' + $el.data( 'booking-title' ) + '</strong><br/>'	+ $el.data( 'booking-time' )
			);

			// Account for the padding in the CSS
			$el.height( $el.height() - 12 );
		} );

} );
